import React from "react";
import "./publication.scss";
import { markedInline } from "../../utils/helpers";


function PublicationX(props) {

  const { publication } = props;
  return (
    <div className="pub mb-4">
    <div className="shadow-sm rounded px-3 py-1 row no-gutters">
      <div className="pub-teaser-container mx-auto col-10 col-md-3 col-sm-4 py-2">
        {publication.teaser ? (
          // <a href={publication.teaser} className="fancybox fancybox.image">
            <img src={publication.teaser} className="pub-teaser" />
          // </a>
        ) : (
          <a>
            <p
              className="pub-teaser"
              dangerouslySetInnerHTML={{ __html: publication.short && markedInline(publication.short) || "" }}
            />
          </a>
        )}
      </div>
      <div className="pub-info col-12 col-md-9 col-sm-8 py-2">
        <h5>
          <a
            href={ publication.link }
            className="pub-title"
            dangerouslySetInnerHTML={{
              __html: markedInline(publication.title),
            }}
          />
        </h5>
        <div
          className="pub-author"
          dangerouslySetInnerHTML={{
            __html: publication.authors.map(markedInline).join(", ") + ".",
          }}
        />
        <div className="pub-venue">
          <span>
            <span className="pub-venue-type">{publication.venue.tag}</span>
            {publication.venue.title}, {publication.date}
            {publication.note &&
              <span dangerouslySetInnerHTML={{ __html: markedInline(publication.note)}}/>
            }.
          </span>
        </div>
        <div>
          <ul>
            {publication.resources.map((resource, i) => (
              <li key={i} className="pub-resource">
                <a href={resource.url}>{resource.type}</a>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
    </div>
  );
}

function PublicationSimple(props) {

  const { publication } = props;
  return (
    <div className="pub mt-3 mb-1">
      <div className="pub-info">
        <h5>
          <span
            className="pub-title"
            dangerouslySetInnerHTML={{
              __html: markedInline(publication.title),
            }}
          />
        </h5>
        <div
          className="pub-author"
          dangerouslySetInnerHTML={{
            __html: publication.authors.map(markedInline).join(", ") + ".",
          }}
        />
        <div className="pub-venue">
          <span>
            <i>{publication.venue.title}</i>, {publication.date}
            {publication.note &&
              <span dangerouslySetInnerHTML={{ __html: markedInline(publication.note)}}/>
            }.
          </span>
        </div>
      </div>
    </div>
  );
}

